import React, { useState, useEffect } from 'react';
import logo from '../assets/0xgpt_logoF.svg';
import { NavHashLink } from 'react-router-hash-link';
import { useGlitch } from 'react-powerglitch'
import AOS from 'aos';
import 'aos/dist/aos.css';



const Sidebar = ({children}) => {

    const glitch = useGlitch(
        {
            "playMode": "hover",
            "createContainers": true,
            "hideOverflow": false,
            "timing": {
              "duration": 250,
              "iterations": 6
            },
            "glitchTimeSpan": {
              "start": 0,
              "end": 1
            },
            "shake": {
              "velocity": 15,
              "amplitudeX": 0.2,
              "amplitudeY": 0.2
            },
            "slice": {
              "count": 6,
              "velocity": 15,
              "minHeight": 0.02,
              "maxHeight": 0.15,
              "hueRotate": true
            },
            "pulse": false
          }
        );
       
    useEffect(() => {
        AOS.init({duration: 1000});
      }, [])

      const[isOpen ,setIsOpen] = useState(false);
      const toggle = () => setIsOpen (!isOpen);

      const menuItem=[
        {
            path:'/page#Gallery',
            name:"Go to Renders",
           
        },
      ]


    return (
        <div className="container"   >
            <div style={{   height: isOpen ? "70px" : "70px", 
                            opacity: isOpen ? "1" : "0.7", 
                            border: isOpen ? "1px solid cyan" : "",
                            width: isOpen ? "" : ""}} className="sidebar" activeclassName="active" activeClass='active'>
             {
                   menuItem.map((item, index)=>(  
               <NavHashLink smooth to={item.path} key={index}>
                <button ref={glitch.ref} className='topbutton2'  activeclassName='active' style={{height:'30px', width:'auto', paddingLeft:'0.5em', paddingRight:'0.5em', marginLeft:'0', marginRight:'0'}}   onClick={toggle}>{item.name}</button>
                </NavHashLink>    ))
               }
            </div>


           <main>{children}</main>
          
        
        </div>
    );
};

export default Sidebar;