import React from 'react';

import { FullpageAccordion, Panel } from "react-fullpage-accordion";
import "react-fullpage-accordion/dist/react-fullpage-accordion.css";
import MoMoRender from '../assets/Renders/MoMoforAccordion.webp';
import SelfRender from '../assets/Renders/SelfRender.webp';


const AccordeonHome = () => {
    
    return (
        <div className=''>
         
         <FullpageAccordion>
        <Panel
          itemId="0"
          background={MoMoRender}
        >
          <p>Give</p>
          <p>
            <a href="/">Relaxing</a>
          </p>
          <p>a try!</p>
        </Panel>
        <Panel
          itemId="1"
          background={SelfRender}
        >
          <p>Give</p>
          <p>
            <a href="/">Foxes</a>
          </p>
          <p>a try!</p>
        </Panel>
        <Panel
          itemId="2"
          background="https://source.unsplash.com/ZtZ2F9JB1SQ/1500x1500"
        >
          <p>Give</p>
          <p>
            <a href="/">Coffee</a>
          </p>
          <p>a try!</p>
        </Panel>
      </FullpageAccordion>

         </div>
    );
};

export default AccordeonHome;